import React, { useState, useEffect } from 'react';
import { FiSave, FiActivity, FiCalendar, FiUser } from 'react-icons/fi';
import { supabase } from '../lib/supabaseClient';

const RegistroSinais = ({ pacienteId }) => {
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState({ type: '', text: '' });
  const [pacientes, setPacientes] = useState([]);
  const [registro, setRegistro] = useState({
    paciente_id: pacienteId || '',
    data_hora: new Date().toISOString().slice(0, 16),
    pressao_arterial: '',
    temperatura: '',
    frequencia_cardiaca: '',
    frequencia_respiratoria: '',
    saturacao: '',
    glicemia: '',
    observacoes: ''
  });

  useEffect(() => {
    fetchPacientes();
    if (pacienteId) {
      setRegistro(prev => ({ ...prev, paciente_id: pacienteId }));
    }
  }, [pacienteId]);

  const fetchPacientes = async () => {
    try {
      setLoading(true);
      const { data, error } = await supabase
        .from('pacientes')
        .select('id, nome')
        .order('nome');

      if (error) throw error;
      if (data) setPacientes(data);
    } catch (error) {
      setMessage({ type: 'danger', text: 'Erro ao carregar lista de pacientes: ' + error.message });
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setRegistro(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      setLoading(true);
      
      // Validao bsica
      if (!registro.paciente_id || !registro.data_hora) {
        setMessage({ type: 'danger', text: 'Por favor, preencha todos os campos obrigatrios.' });
        return;
      }

      const { error } = await supabase
        .from('sinais_vitais')
        .insert([registro]);

      if (error) throw error;
      
      setMessage({ type: 'success', text: 'Registro de sinais vitais salvo com sucesso!' });
      
      // Limpar formulrio mantendo apenas o paciente selecionado
      setRegistro({
        paciente_id: registro.paciente_id,
        data_hora: new Date().toISOString().slice(0, 16),
        pressao_arterial: '',
        temperatura: '',
        frequencia_cardiaca: '',
        frequencia_respiratoria: '',
        saturacao: '',
        glicemia: '',
        observacoes: ''
      });
    } catch (error) {
      setMessage({ type: 'danger', text: 'Erro ao salvar registro: ' + error.message });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="card">
      <div className="card-header">
        <h2 className="card-title">Registro de Sinais Vitais</h2>
      </div>
      
      {message.text && (
        <div className={`alert alert-${message.type}`}>
          {message.text}
        </div>
      )}
      
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="paciente_id">Paciente*</label>
          <div className="flex items-center">
            <FiUser className="mr-2 text-secondary" />
            <select
              id="paciente_id"
              name="paciente_id"
              className="form-control"
              value={registro.paciente_id}
              onChange={handleChange}
              required
              disabled={!!pacienteId}
            >
              <option value="">Selecione um paciente</option>
              {pacientes.map(paciente => (
                <option key={paciente.id} value={paciente.id}>
                  {paciente.nome}
                </option>
              ))}
            </select>
          </div>
        </div>
        
        <div className="form-group">
          <label htmlFor="data_hora">Data e Hora*</label>
          <div className="flex items-center">
            <FiCalendar className="mr-2 text-secondary" />
            <input
              type="datetime-local"
              id="data_hora"
              name="data_hora"
              className="form-control"
              value={registro.data_hora}
              onChange={handleChange}
              required
            />
          </div>
        </div>
        
        <div className="grid grid-cols-2 gap-4">
          <div className="form-group">
            <label htmlFor="pressao_arterial">Presso Arterial (mmHg)</label>
            <div className="flex items-center">
              <FiActivity className="mr-2 text-secondary" />
              <input
                type="text"
                id="pressao_arterial"
                name="pressao_arterial"
                className="form-control"
                placeholder="Ex: 120/80"
                value={registro.pressao_arterial}
                onChange={handleChange}
              />
            </div>
          </div>
          
          <div className="form-group">
            <label htmlFor="temperatura">Temperatura (C)</label>
            <input
              type="number"
              id="temperatura"
              name="temperatura"
              className="form-control"
              step="0.1"
              placeholder="Ex: 36.5"
              value={registro.temperatura}
              onChange={handleChange}
            />
          </div>
        </div>
        
        <div className="grid grid-cols-2 gap-4">
          <div className="form-group">
            <label htmlFor="frequencia_cardiaca">Frequncia Cardaca (bpm)</label>
            <input
              type="number"
              id="frequencia_cardiaca"
              name="frequencia_cardiaca"
              className="form-control"
              placeholder="Ex: 80"
              value={registro.frequencia_cardiaca}
              onChange={handleChange}
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="frequencia_respiratoria">Frequncia Respiratria (irpm)</label>
            <input
              type="number"
              id="frequencia_respiratoria"
              name="frequencia_respiratoria"
              className="form-control"
              placeholder="Ex: 16"
              value={registro.frequencia_respiratoria}
              onChange={handleChange}
            />
          </div>
        </div>
        
        <div className="grid grid-cols-2 gap-4">
          <div className="form-group">
            <label htmlFor="saturacao">Saturao O (%)</label>
            <input
              type="number"
              id="saturacao"
              name="saturacao"
              className="form-control"
              placeholder="Ex: 98"
              value={registro.saturacao}
              onChange={handleChange}
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="glicemia">Glicemia (mg/dL)</label>
            <input
              type="number"
              id="glicemia"
              name="glicemia"
              className="form-control"
              placeholder="Ex: 100"
              value={registro.glicemia}
              onChange={handleChange}
            />
          </div>
        </div>
        
        <div className="form-group">
          <label htmlFor="observacoes">Observaes</label>
          <textarea
            id="observacoes"
            name="observacoes"
            className="form-control"
            rows="3"
            value={registro.observacoes}
            onChange={handleChange}
          ></textarea>
        </div>
        
        <button 
          type="submit" 
          className="btn-save" 
          disabled={loading}
        >
          <FiSave />
          {loading ? 'Salvando...' : 'Salvar Registro'}
        </button>
      </form>
    </div>
  );
};

export default RegistroSinais;
