import React, { useState, useEffect } from 'react';
import { FiFileText, FiDownload, FiCalendar, FiUser, FiActivity, FiSave } from 'react-icons/fi';
import { supabase } from '../lib/supabaseClient';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, BarElement, Title, Tooltip, Legend } from 'chart.js';
import { Line, Bar } from 'react-chartjs-2';

// Registrar componentes do Chart.js
ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, BarElement, Title, Tooltip, Legend);

const Relatorios = () => {
  const [loading, setLoading] = useState(false);
  const [pacientes, setPacientes] = useState([]);
  const [filtros, setFiltros] = useState({
    tipo: 'sinais_vitais',
    paciente_id: '',
    data_inicio: new Date(new Date().setDate(new Date().getDate() - 30)).toISOString().slice(0, 10),
    data_fim: new Date().toISOString().slice(0, 10)
  });
  const [dadosRelatorio, setDadosRelatorio] = useState(null);
  const [message, setMessage] = useState({ type: '', text: '' });

  useEffect(() => {
    fetchPacientes();
  }, []);

  const fetchPacientes = async () => {
    try {
      setLoading(true);
      const { data, error } = await supabase
        .from('pacientes')
        .select('id, nome')
        .order('nome');

      if (error) throw error;
      setPacientes(data || []);
    } catch (error) {
      console.error('Erro ao carregar pacientes:', error.message);
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFiltros(prev => ({ ...prev, [name]: value }));
  };

  const gerarRelatorio = async () => {
    try {
      setLoading(true);
      setMessage({ type: '', text: '' });
      
      // Validao bsica
      if (!filtros.tipo || (!filtros.data_inicio && !filtros.data_fim)) {
        setMessage({ type: 'danger', text: 'Por favor, preencha os filtros obrigatrios.' });
        return;
      }

      if (filtros.tipo === 'sinais_vitais') {
        await gerarRelatorioSinaisVitais();
      } else if (filtros.tipo === 'agendamentos') {
        await gerarRelatorioAgendamentos();
      } else if (filtros.tipo === 'pacientes') {
        await gerarRelatorioPacientes();
      }
      
    } catch (error) {
      setMessage({ type: 'danger', text: 'Erro ao gerar relatrio: ' + error.message });
      console.error('Erro ao gerar relatrio:', error);
    } finally {
      setLoading(false);
    }
  };

  const gerarRelatorioSinaisVitais = async () => {
    let query = supabase
      .from('sinais_vitais')
      .select(`
        *,
        pacientes:paciente_id (nome)
      `)
      .order('data_hora');
    
    if (filtros.paciente_id) {
      query = query.eq('paciente_id', filtros.paciente_id);
    }
    
    if (filtros.data_inicio) {
      const dataInicio = new Date(filtros.data_inicio);
      dataInicio.setHours(0, 0, 0, 0);
      query = query.gte('data_hora', dataInicio.toISOString());
    }
    
    if (filtros.data_fim) {
      const dataFim = new Date(filtros.data_fim);
      dataFim.setHours(23, 59, 59, 999);
      query = query.lte('data_hora', dataFim.toISOString());
    }
    
    const { data, error } = await query;
    
    if (error) throw error;
    
    if (!data || data.length === 0) {
      setMessage({ type: 'warning', text: 'Nenhum dado encontrado para os filtros selecionados.' });
      setDadosRelatorio(null);
      return;
    }
    
    // Preparar dados para o grfico
    const registrosPorData = {};
    data.forEach(registro => {
      const data = new Date(registro.data_hora).toLocaleDateString('pt-BR');
      if (!registrosPorData[data]) {
        registrosPorData[data] = 0;
      }
      registrosPorData[data]++;
    });
    
    const labels = Object.keys(registrosPorData);
    const valores = Object.values(registrosPorData);
    
    const chartData = {
      labels,
      datasets: [
        {
          label: 'Registros de Sinais Vitais',
          data: valores,
          borderColor: '#0ea5e9',
          backgroundColor: 'rgba(14, 165, 233, 0.2)',
          tension: 0.4
        }
      ]
    };
    
    setDadosRelatorio({
      tipo: 'sinais_vitais',
      titulo: 'Relatrio de Sinais Vitais',
      periodo: `${new Date(filtros.data_inicio).toLocaleDateString('pt-BR')} a ${new Date(filtros.data_fim).toLocaleDateString('pt-BR')}`,
      paciente: filtros.paciente_id ? pacientes.find(p => p.id === filtros.paciente_id)?.nome : 'Todos',
      dados: data,
      chartData,
      chartType: 'line'
    });
  };

  const gerarRelatorioAgendamentos = async () => {
    let query = supabase
      .from('agendamentos')
      .select(`
        *,
        pacientes:paciente_id (nome)
      `)
      .order('data')
      .order('hora');
    
    if (filtros.paciente_id) {
      query = query.eq('paciente_id', filtros.paciente_id);
    }
    
    if (filtros.data_inicio) {
      query = query.gte('data', filtros.data_inicio);
    }
    
    if (filtros.data_fim) {
      query = query.lte('data', filtros.data_fim);
    }
    
    const { data, error } = await query;
    
    if (error) throw error;
    
    if (!data || data.length === 0) {
      setMessage({ type: 'warning', text: 'Nenhum dado encontrado para os filtros selecionados.' });
      setDadosRelatorio(null);
      return;
    }
    
    // Preparar dados para o grfico
    const agendamentosPorTipo = {
      visita: 0,
      consulta: 0,
      procedimento: 0,
      outro: 0
    };
    
    data.forEach(agendamento => {
      agendamentosPorTipo[agendamento.tipo]++;
    });
    
    const labels = Object.keys(agendamentosPorTipo).map(tipo => {
      switch (tipo) {
        case 'visita': return 'Visita Domiciliar';
        case 'consulta': return 'Consulta';
        case 'procedimento': return 'Procedimento';
        case 'outro': return 'Outro';
        default: return tipo;
      }
    });
    
    const valores = Object.values(agendamentosPorTipo);
    
    const chartData = {
      labels,
      datasets: [
        {
          label: 'Agendamentos por Tipo',
          data: valores,
          backgroundColor: [
            'rgba(59, 130, 246, 0.6)',
            'rgba(239, 68, 68, 0.6)',
            'rgba(245, 158, 11, 0.6)',
            'rgba(16, 185, 129, 0.6)'
          ],
          borderWidth: 1
        }
      ]
    };
    
    setDadosRelatorio({
      tipo: 'agendamentos',
      titulo: 'Relatrio de Agendamentos',
      periodo: `${new Date(filtros.data_inicio).toLocaleDateString('pt-BR')} a ${new Date(filtros.data_fim).toLocaleDateString('pt-BR')}`,
      paciente: filtros.paciente_id ? pacientes.find(p => p.id === filtros.paciente_id)?.nome : 'Todos',
      dados: data,
      chartData,
      chartType: 'bar'
    });
  };

  const gerarRelatorioPacientes = async () => {
    const { data, error } = await supabase
      .from('pacientes')
      .select('*')
      .order('nome');
    
    if (error) throw error;
    
    if (!data || data.length === 0) {
      setMessage({ type: 'warning', text: 'Nenhum paciente encontrado.' });
      setDadosRelatorio(null);
      return;
    }
    
    // Preparar dados para o grfico
    const pacientesPorCategoria = {
      domiciliar: 0,
      hospitalar: 0,
      freelancer: 0
    };
    
    data.forEach(paciente => {
      pacientesPorCategoria[paciente.categoria]++;
    });
    
    const labels = Object.keys(pacientesPorCategoria).map(categoria => {
      switch (categoria) {
        case 'domiciliar': return 'Atendimento Domiciliar';
        case 'hospitalar': return 'Atendimento Hospitalar';
        case 'freelancer': return 'Atendimento Freelancer';
        default: return categoria;
      }
    });
    
    const valores = Object.values(pacientesPorCategoria);
    
    const chartData = {
      labels,
      datasets: [
        {
          label: 'Pacientes por Categoria',
          data: valores,
          backgroundColor: [
            'rgba(59, 130, 246, 0.6)',
            'rgba(239, 68, 68, 0.6)',
            'rgba(245, 158, 11, 0.6)'
          ],
          borderWidth: 1
        }
      ]
    };
    
    setDadosRelatorio({
      tipo: 'pacientes',
      titulo: 'Relatrio de Pacientes',
      periodo: 'Todos os pacientes cadastrados',
      dados: data,
      chartData,
      chartType: 'bar'
    });
  };

  const exportarPDF = () => {
    setMessage({ type: 'warning', text: 'Funcionalidade de exportao para PDF em desenvolvimento.' });
  };

  const exportarCSV = () => {
    if (!dadosRelatorio || !dadosRelatorio.dados) {
      setMessage({ type: 'danger', text: 'Nenhum dado disponvel para exportao.' });
      return;
    }
    
    let csvContent = '';
    const dados = dadosRelatorio.dados;
    
    if (dadosRelatorio.tipo === 'sinais_vitais') {
      csvContent = 'Data/Hora,Paciente,Presso Arterial,Temperatura,Freq. Cardaca,Freq. Respiratria,Saturao,Glicemia,Observaes\n';
      
      dados.forEach(registro => {
        const linha = [
          new Date(registro.data_hora).toLocaleString('pt-BR'),
          registro.pacientes?.nome || '',
          registro.pressao_arterial || '',
          registro.temperatura || '',
          registro.frequencia_cardiaca || '',
          registro.frequencia_respiratoria || '',
          registro.saturacao || '',
          registro.glicemia || '',
          registro.observacoes || ''
        ].map(valor => `"${valor}"`).join(',');
        
        csvContent += linha + '\n';
      });
    } else if (dadosRelatorio.tipo === 'agendamentos') {
      csvContent = 'Data,Hora,Paciente,Tipo,Endereo,Observaes\n';
      
      dados.forEach(agendamento => {
        const linha = [
          agendamento.data,
          agendamento.hora,
          agendamento.pacientes?.nome || '',
          agendamento.tipo,
          agendamento.endereco || '',
          agendamento.observacoes || ''
        ].map(valor => `"${valor}"`).join(',');
        
        csvContent += linha + '\n';
      });
    } else if (dadosRelatorio.tipo === 'pacientes') {
      csvContent = 'Nome,Idade,Telefone,Endereo,Convnio,Categoria,Observaes\n';
      
      dados.forEach(paciente => {
        const linha = [
          paciente.nome,
          paciente.idade || '',
          paciente.telefone || '',
          paciente.endereco || '',
          paciente.convenio || '',
          paciente.categoria,
          paciente.observacoes || ''
        ].map(valor => `"${valor}"`).join(',');
        
        csvContent += linha + '\n';
      });
    }
    
    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.setAttribute('href', url);
    link.setAttribute('download', `relatorio_${dadosRelatorio.tipo}_${new Date().toISOString().slice(0, 10)}.csv`);
    link.style.visibility = 'hidden';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <div>
      <h1 className="text-xl font-bold mb-4">Relatrios</h1>
      
      <div className="card mb-4">
        <div className="card-header">
          <h2 className="card-title">Filtros</h2>
        </div>
        
        {message.text && (
          <div className={`alert alert-${message.type} mb-4`}>
            {message.text}
          </div>
        )}
        
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-4">
          <div className="form-group">
            <label htmlFor="tipo">Tipo de Relatrio*</label>
            <select
              id="tipo"
              name="tipo"
              className="form-control"
              value={filtros.tipo}
              onChange={handleChange}
              required
            >
              <option value="sinais_vitais">Sinais Vitais</option>
              <option value="agendamentos">Agendamentos</option>
              <option value="pacientes">Pacientes</option>
            </select>
          </div>
          
          <div className="form-group">
            <label htmlFor="paciente_id">Paciente</label>
            <div className="flex items-center">
              <FiUser className="mr-2 text-secondary" />
              <select
                id="paciente_id"
                name="paciente_id"
                className="form-control"
                value={filtros.paciente_id}
                onChange={handleChange}
                disabled={filtros.tipo === 'pacientes'}
              >
                <option value="">Todos os pacientes</option>
                {pacientes.map(paciente => (
                  <option key={paciente.id} value={paciente.id}>
                    {paciente.nome}
                  </option>
                ))}
              </select>
            </div>
          </div>
          
          <div className="form-group">
            <label htmlFor="data_inicio">Data Inicial</label>
            <div className="flex items-center">
              <FiCalendar className="mr-2 text-secondary" />
              <input
                type="date"
                id="data_inicio"
                name="data_inicio"
                className="form-control"
                value={filtros.data_inicio}
                onChange={handleChange}
                disabled={filtros.tipo === 'pacientes'}
              />
            </div>
          </div>
          
          <div className="form-group">
            <label htmlFor="data_fim">Data Final</label>
            <div className="flex items-center">
              <FiCalendar className="mr-2 text-secondary" />
              <input
                type="date"
                id="data_fim"
                name="data_fim"
                className="form-control"
                value={filtros.data_fim}
                onChange={handleChange}
                disabled={filtros.tipo === 'pacientes'}
              />
            </div>
          </div>
        </div>
        
        <div className="flex gap-2">
          <button 
            onClick={gerarRelatorio} 
            className="btn-save" 
            disabled={loading}
          >
            <FiFileText />
            {loading ? 'Gerando...' : 'Gerar Relatrio'}
          </button>
        </div>
      </div>
      
      {dadosRelatorio && (
        <div className="card">
          <div className="card-header flex justify-between items-center">
            <h2 className="card-title">{dadosRelatorio.titulo}</h2>
            <div className="flex gap-2">
              <button 
                onClick={exportarPDF} 
                className="btn btn-secondary btn-sm flex items-center gap-1"
                title="Exportar para PDF"
              >
                <FiDownload size={14} /> PDF
              </button>
              <button 
                onClick={exportarCSV} 
                className="btn btn-primary btn-sm flex items-center gap-1"
                title="Exportar para CSV"
              >
                <FiDownload size={14} /> CSV
              </button>
            </div>
          </div>
          
          <div className="p-4">
            <div className="mb-4">
              <p><strong>Perodo:</strong> {dadosRelatorio.periodo}</p>
              {dadosRelatorio.paciente && (
                <p><strong>Paciente:</strong> {dadosRelatorio.paciente}</p>
              )}
              <p><strong>Total de registros:</strong> {dadosRelatorio.dados.length}</p>
            </div>
            
            <div className="mb-4" style={{ height: '300px' }}>
              {dadosRelatorio.chartType === 'line' ? (
                <Line 
                  data={dadosRelatorio.chartData} 
                  options={{ 
                    maintainAspectRatio: false,
                    scales: {
                      y: {
                        beginAtZero: true
                      }
                    }
                  }} 
                />
              ) : (
                <Bar 
                  data={dadosRelatorio.chartData} 
                  options={{ 
                    maintainAspectRatio: false,
                    scales: {
                      y: {
                        beginAtZero: true
                      }
                    }
                  }} 
                />
              )}
            </div>
            
            <div className="overflow-x-auto">
              <table className="table">
                <thead>
                  <tr>
                    {dadosRelatorio.tipo === 'sinais_vitais' && (
                      <>
                        <th>Data/Hora</th>
                        <th>Paciente</th>
                        <th>Presso</th>
                        <th>Temp.</th>
                        <th>FC</th>
                        <th>FR</th>
                        <th>Sat. O</th>
                        <th>Glicemia</th>
                      </>
                    )}
                    {dadosRelatorio.tipo === 'agendamentos' && (
                      <>
                        <th>Data</th>
                        <th>Hora</th>
                        <th>Paciente</th>
                        <th>Tipo</th>
                        <th>Endereo</th>
                      </>
                    )}
                    {dadosRelatorio.tipo === 'pacientes' && (
                      <>
                        <th>Nome</th>
                        <th>Telefone</th>
                        <th>Convnio</th>
                        <th>Categoria</th>
                      </>
                    )}
                  </tr>
                </thead>
                <tbody>
                  {dadosRelatorio.tipo === 'sinais_vitais' && dadosRelatorio.dados.map(registro => (
                    <tr key={registro.id}>
                      <td>{new Date(registro.data_hora).toLocaleString('pt-BR')}</td>
                      <td>{registro.pacientes?.nome}</td>
                      <td>{registro.pressao_arterial || '-'}</td>
                      <td>{registro.temperatura ? `${registro.temperatura} C` : '-'}</td>
                      <td>{registro.frequencia_cardiaca ? `${registro.frequencia_cardiaca} bpm` : '-'}</td>
                      <td>{registro.frequencia_respiratoria ? `${registro.frequencia_respiratoria} irpm` : '-'}</td>
                      <td>{registro.saturacao ? `${registro.saturacao}%` : '-'}</td>
                      <td>{registro.glicemia ? `${registro.glicemia} mg/dL` : '-'}</td>
                    </tr>
                  ))}
                  {dadosRelatorio.tipo === 'agendamentos' && dadosRelatorio.dados.map(agendamento => (
                    <tr key={agendamento.id}>
                      <td>{new Date(agendamento.data).toLocaleDateString('pt-BR')}</td>
                      <td>{agendamento.hora}</td>
                      <td>{agendamento.pacientes?.nome}</td>
                      <td>{agendamento.tipo.charAt(0).toUpperCase() + agendamento.tipo.slice(1)}</td>
                      <td>{agendamento.endereco || '-'}</td>
                    </tr>
                  ))}
                  {dadosRelatorio.tipo === 'pacientes' && dadosRelatorio.dados.map(paciente => (
                    <tr key={paciente.id}>
                      <td>{paciente.nome}</td>
                      <td>{paciente.telefone || '-'}</td>
                      <td>{paciente.convenio || '-'}</td>
                      <td>
                        <span className={`patient-category category-${paciente.categoria}`}>
                          {paciente.categoria === 'domiciliar' ? 'Domiciliar' : 
                           paciente.categoria === 'hospitalar' ? 'Hospitalar' : 'Freelancer'}
                        </span>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Relatorios;
