import React, { useState, useEffect } from 'react';
import { FiCalendar, FiClock, FiUser, FiMapPin, FiSave } from 'react-icons/fi';
import { supabase } from '../lib/supabaseClient';

const Agendamentos = () => {
  const [loading, setLoading] = useState(false);
  const [pacientes, setPacientes] = useState([]);
  const [agendamentos, setAgendamentos] = useState([]);
  const [message, setMessage] = useState({ type: '', text: '' });
  const [novoAgendamento, setNovoAgendamento] = useState({
    paciente_id: '',
    data: new Date().toISOString().slice(0, 10),
    hora: '08:00',
    endereco: '',
    tipo: 'visita',
    observacoes: ''
  });
  const [filtroData, setFiltroData] = useState(new Date().toISOString().slice(0, 10));

  useEffect(() => {
    fetchPacientes();
    fetchAgendamentos();
  }, []);

  useEffect(() => {
    fetchAgendamentos();
  }, [filtroData]);

  const fetchPacientes = async () => {
    try {
      setLoading(true);
      const { data, error } = await supabase
        .from('pacientes')
        .select('id, nome')
        .order('nome');

      if (error) throw error;
      setPacientes(data || []);
    } catch (error) {
      console.error('Erro ao carregar pacientes:', error.message);
    } finally {
      setLoading(false);
    }
  };

  const fetchAgendamentos = async () => {
    try {
      setLoading(true);
      
      let query = supabase
        .from('agendamentos')
        .select(`
          *,
          pacientes:paciente_id (nome)
        `)
        .order('data')
        .order('hora');
      
      if (filtroData) {
        query = query.eq('data', filtroData);
      }
      
      const { data, error } = await query;
      
      if (error) throw error;
      setAgendamentos(data || []);
    } catch (error) {
      console.error('Erro ao carregar agendamentos:', error.message);
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setNovoAgendamento(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      setLoading(true);
      
      // Validao bsica
      if (!novoAgendamento.paciente_id || !novoAgendamento.data || !novoAgendamento.hora) {
        setMessage({ type: 'danger', text: 'Por favor, preencha todos os campos obrigatrios.' });
        return;
      }

      const { error } = await supabase
        .from('agendamentos')
        .insert([novoAgendamento]);

      if (error) throw error;
      
      setMessage({ type: 'success', text: 'Agendamento salvo com sucesso!' });
      
      // Limpar formulrio
      setNovoAgendamento({
        paciente_id: '',
        data: new Date().toISOString().slice(0, 10),
        hora: '08:00',
        endereco: '',
        tipo: 'visita',
        observacoes: ''
      });
      
      // Atualizar lista
      fetchAgendamentos();
    } catch (error) {
      setMessage({ type: 'danger', text: 'Erro ao salvar agendamento: ' + error.message });
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async (id) => {
    if (!window.confirm('Tem certeza que deseja excluir este agendamento?')) return;
    
    try {
      setLoading(true);
      const { error } = await supabase
        .from('agendamentos')
        .delete()
        .eq('id', id);
      
      if (error) throw error;
      
      setAgendamentos(agendamentos.filter(a => a.id !== id));
      setMessage({ type: 'success', text: 'Agendamento excludo com sucesso!' });
    } catch (error) {
      setMessage({ type: 'danger', text: 'Erro ao excluir agendamento: ' + error.message });
    } finally {
      setLoading(false);
    }
  };

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('pt-BR');
  };

  return (
    <div>
      <h1 className="text-xl font-bold mb-4">Agendamentos</h1>
      
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="card">
          <div className="card-header">
            <h2 className="card-title">Novo Agendamento</h2>
          </div>
          
          {message.text && (
            <div className={`alert alert-${message.type} mb-4`}>
              {message.text}
            </div>
          )}
          
          <form onSubmit={handleSubmit}>
            <div className="form-group">
              <label htmlFor="paciente_id">Paciente*</label>
              <div className="flex items-center">
                <FiUser className="mr-2 text-secondary" />
                <select
                  id="paciente_id"
                  name="paciente_id"
                  className="form-control"
                  value={novoAgendamento.paciente_id}
                  onChange={handleChange}
                  required
                >
                  <option value="">Selecione um paciente</option>
                  {pacientes.map(paciente => (
                    <option key={paciente.id} value={paciente.id}>
                      {paciente.nome}
                    </option>
                  ))}
                </select>
              </div>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="form-group">
                <label htmlFor="data">Data*</label>
                <div className="flex items-center">
                  <FiCalendar className="mr-2 text-secondary" />
                  <input
                    type="date"
                    id="data"
                    name="data"
                    className="form-control"
                    value={novoAgendamento.data}
                    onChange={handleChange}
                    required
                  />
                </div>
              </div>
              
              <div className="form-group">
                <label htmlFor="hora">Hora*</label>
                <div className="flex items-center">
                  <FiClock className="mr-2 text-secondary" />
                  <input
                    type="time"
                    id="hora"
                    name="hora"
                    className="form-control"
                    value={novoAgendamento.hora}
                    onChange={handleChange}
                    required
                  />
                </div>
              </div>
            </div>
            
            <div className="form-group">
              <label htmlFor="endereco">Endereo</label>
              <div className="flex items-center">
                <FiMapPin className="mr-2 text-secondary" />
                <input
                  type="text"
                  id="endereco"
                  name="endereco"
                  className="form-control"
                  value={novoAgendamento.endereco}
                  onChange={handleChange}
                />
              </div>
            </div>
            
            <div className="form-group">
              <label htmlFor="tipo">Tipo de Atendimento</label>
              <select
                id="tipo"
                name="tipo"
                className="form-control"
                value={novoAgendamento.tipo}
                onChange={handleChange}
              >
                <option value="visita">Visita Domiciliar</option>
                <option value="consulta">Consulta</option>
                <option value="procedimento">Procedimento</option>
                <option value="outro">Outro</option>
              </select>
            </div>
            
            <div className="form-group">
              <label htmlFor="observacoes">Observaes</label>
              <textarea
                id="observacoes"
                name="observacoes"
                className="form-control"
                rows="3"
                value={novoAgendamento.observacoes}
                onChange={handleChange}
              ></textarea>
            </div>
            
            <button 
              type="submit" 
              className="btn-save" 
              disabled={loading}
            >
              <FiSave />
              {loading ? 'Salvando...' : 'Salvar Agendamento'}
            </button>
          </form>
        </div>
        
        <div className="card">
          <div className="card-header">
            <h2 className="card-title">Agenda do Dia</h2>
          </div>
          
          <div className="form-group mb-4">
            <label htmlFor="filtroData">Selecione a Data</label>
            <div className="flex items-center">
              <FiCalendar className="mr-2 text-secondary" />
              <input
                type="date"
                id="filtroData"
                className="form-control"
                value={filtroData}
                onChange={(e) => setFiltroData(e.target.value)}
              />
            </div>
          </div>
          
          {loading ? (
            <div className="text-center p-4">Carregando agendamentos...</div>
          ) : agendamentos.length === 0 ? (
            <div className="text-center p-4">Nenhum agendamento para esta data.</div>
          ) : (
            <div className="overflow-y-auto" style={{ maxHeight: '500px' }}>
              {agendamentos.map(agendamento => (
                <div key={agendamento.id} className="p-3 border-b flex justify-between items-center">
                  <div>
                    <div className="font-semibold">{agendamento.hora} - {agendamento.pacientes?.nome}</div>
                    <div className="text-sm">{agendamento.tipo.charAt(0).toUpperCase() + agendamento.tipo.slice(1)}</div>
                    {agendamento.endereco && (
                      <div className="text-sm flex items-center gap-1">
                        <FiMapPin size={12} /> {agendamento.endereco}
                      </div>
                    )}
                    {agendamento.observacoes && (
                      <div className="text-sm mt-1">{agendamento.observacoes}</div>
                    )}
                  </div>
                  <button 
                    onClick={() => handleDelete(agendamento.id)} 
                    className="btn btn-danger btn-sm"
                  >
                    Cancelar
                  </button>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Agendamentos;
