import React, { useState } from 'react';
import { FiUser, FiLock } from 'react-icons/fi';
import { useAuth } from '../context/AuthContext';
import { useNavigate } from 'react-router-dom';

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const { signIn } = useAuth();
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setLoading(true);

    try {
      const { error } = await signIn({ email, password });
      
      if (error) throw error;
      
      navigate('/dashboard');
    } catch (error) {
      setError('Falha no login. Verifique seu email e senha.');
      console.error('Erro de login:', error.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="login-container">
      <img src="/images/logo.jpeg" alt="Macena Health" className="login-logo" />
      
      <div className="login-card">
        <h1 className="login-title">Macena Health</h1>
        <h2 className="text-center mb-4">Cuidados domiciliares</h2>
        
        {error && (
          <div className="alert alert-danger">
            {error}
          </div>
        )}
        
        <form onSubmit={handleSubmit}>
          <div className="form-group">
            <label htmlFor="email">Email</label>
            <div className="flex items-center">
              <FiUser className="mr-2 text-secondary" />
              <input
                type="email"
                id="email"
                className="form-control"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>
          </div>
          
          <div className="form-group">
            <label htmlFor="password">Senha</label>
            <div className="flex items-center">
              <FiLock className="mr-2 text-secondary" />
              <input
                type="password"
                id="password"
                className="form-control"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>
          </div>
          
          <button 
            type="submit" 
            className="btn btn-primary w-100 mt-4" 
            disabled={loading}
            style={{ width: '100%' }}
          >
            {loading ? 'Entrando...' : 'Entrar'}
          </button>
        </form>
      </div>
    </div>
  );
};

export default Login;
